.App {
  background-color: #1b274a;
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  color: white !important;
}

.App-header {
  font-size: 28px;
  margin-top: 1.5em;
  font-weight: 600;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.wrapper {
  border: 1px solid white;
  border-radius: 8px;
  padding: 2em;
  margin-top: 3em;
  margin-left: 1em;
  margin-right: 1em;
  box-sizing: border-box;
  width: 410px;
}

.wrapper.no-border {
  border: 0;
  padding: 0;
}

@media (max-width: 450px) {
  .wrapper {
    width: unset;
  }
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
}

.MuiSelect-select {
  color: white;
}
.white-outline fieldset.MuiOutlinedInput-notchedOutline {
  border-color: white !important;
}

.MuiFormControl-root {
  width: 100%;
}

.error {
  color: red;
  margin-top: 4px;
  font-size: 12px;
}

p {
  margin: 0 0 1em 0;
}

.emphasis {
  font-weight: 600;
  color: #ccccff;
  /* text-decoration: underline; */
}
